/* ------------ MOBILE ------------ */
.container {
    margin-left: auto;
    margin-right: auto;
}

.container-mob-menu {
    position: relative;
}

.menu,
.logo-text {
    display: none;
}

.top {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: inherit;
    height: 48px;
    z-index: 100;
}

.switch_theme {
    background-color: inherit;
    border: none;
    width: 44px;
    height: 18px;
}

.menu-item {
    font-family: 'Roboto Medium';
    font-weight: 500;
    font-size: 12px;
    line-height: 1.7;
    display: flex;
    align-items: center;
    text-align: center;
    text-transform: uppercase;
    color: #595959;
}

.is-hidden-menu {
    opacity: 0;
    pointer-events: none;
}

.hidden-menu {
    display: none;
}

/* <!--------------- MOBILE MENU -----------------> */
.backdrop {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 20;
    width: 100vw;
    height: 100vh;
    background: rgba(255, 255, 255, 0.2);
    display: none;
}

.container {
    position: relative;
}

.mob-menu {
    position: absolute;
    top: 0;
    left: 0;
    width: 204px;
    height: 428px;
    background-color: inherit;
    padding-top: 48px;
    background-color: #171717;
    border-radius: 0px 8px 8px 0px;
    padding-top: 8px;
    padding-left: 20px;
    overflow-y: auto;
}

.is-visible {
    display: block;
}

.no-scroll {
    overflow: hidden;
}

.popcorn-mob {
    margin-bottom: 40px;
}

.menu-item-mob {
    font-family: 'Roboto Regular';
    font-size: 14px;
    line-height: 1.14;
    margin-bottom: 24px;
    color: #FFFFFF;
}

.current {
    color: #F87719;
}

/* ---------- TABLET ------------ */

@media screen and (min-width: 768px) {
    .container {
        max-width: 768px;
        padding: 6px 32px;
    }

    .menu {
        display: flex;
        gap: 32px;
    }

    .top {
        height: 60px;
    }

    .menu-menu {
        display: none;
    }

    .mob-menu {
        display: none;
    }

    .logo {
        display: flex;
        gap: 8px;
    }

    .popcorn {
        width: 48px;
        height: 48px;
    }

    .logo-text {
        font-family: 'Roboto Regular';
        font-size: 24px;
        line-height: 1.17;
        display: flex;
        align-items: center;
        text-align: center;
        margin-right: 59px;
        color: #F87719;
    }

    .switch_theme {
        width: 60px;
        height: 24px;
        margin-left: 169px;
    }

    .menu-item {
        font-size: 14px;
        line-height: 1.14;
        height: 100%;
    }

    .menu {
        height: 100%;
    }
}

/* ---------- DESKTOP ------------ */

@media screen and (min-width: 1280px) {
    .container {
        max-width: 1280px;
    }
}

.light-theme {
    background-color: #F5F5F5;
}

.light-theme .logo-text {
    color: #282828;
}

.light-theme .menu-item {
    color: #595959;
}

.light-theme .menu-item-mob {
    color: #595959;
}

.light-theme .current {
    color:#F87719;
}